.modal {
    height: 100vh;
    width: 100vw;
    background-color: rgba(0, 0, 0, 0.4);
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    opacity: 0;
    pointer-events: none;
}

.modal_content {
    background-color: white;
    max-height: 600px;
    max-width: 420px;
    transform: scale(.5);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 8px;
}


.modal.active {
    pointer-events: all;
    opacity: 1;
    z-index: 999;
}

.modal_content.active {
    transform: scale(1);
}
@media screen and (max-width: 543px){
    .modal_content{
        height: 80%;
        width: 85%
    }
}